<template>
  <v-app id="schedule" light class="calendar-app">
    <v-sheet tile dark height="41">
      <v-toolbar flat height="40">
        <v-btn icon @click="$refs.calendar.prev()" style="margin-left:10%"color="#29B6F6">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
        <v-toolbar-title v-if="$refs.calendar">{{ $refs.calendar.title }}</v-toolbar-title>
        <v-btn text color="#B2FF59" @click="setToday" style="margin-left:10%">Today</v-btn>
        <v-btn text color="#B2FF59" @click="setType('month')" style="margin-left:20px">月视图</v-btn>
        <v-btn text color="#B2FF59" @click="setType('week')" style="margin-left:20px">周视图</v-btn>
        <v-btn text color="#B2FF59" @click="setType('day')" style="margin-left:20px">日视图</v-btn>
        <v-spacer></v-spacer>
        <v-btn icon color="#26A69A" @click="$refs.calendar.next()" style="margin-right:10%">
          <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </v-toolbar>
    </v-sheet>
    <v-sheet height="80%" width="90%" class="calendar-sheet">
      <v-calendar
        light
        ref="calendar"
        v-model="focus"
        color="#EA80FC"
        :weekdays="weekday"
        :type="type"
        :now="today"
        :events="events"
        :event-overlap-mode="mode"
        :event-overlap-threshold="30"
        :event-color="getEventColor"
        @change="getEvents"
        @click:event="showEvent"
        @click:more="viewDay"
        @click:date="viewDay"
      ></v-calendar>
      <v-menu
          v-model="selectedOpen"
          :close-on-content-click="false"
          :activator="selectedElement"
          offset-x
        >
          <v-card min-width="320px" flat dark>
            <v-toolbar height="40px">
              <v-toolbar-title>任务详情</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn icon color="#FF8A80" @click="selectedOpen = false"><v-icon>fa-times-circle</v-icon></v-btn>
            </v-toolbar>
            <v-card-text>
              <v-list dense>
                <v-list-item-group v-model="listItem" color="#1DE9B6">
                  <v-list-item v-for="(val, key, i) in eventTitle" :key="i">
                    <v-list-item-title>{{ val }}: </v-list-item-title>
                    <v-list-item-subtitle><i style="color:#F5F5F5">{{ selectedEvent[key] }}</i>
                    </v-list-item-subtitle>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-card-text>
          </v-card>
        </v-menu>
    </v-sheet>
  </v-app>
</template>

<script>
  import SCHEDULE from '@/config/schedule.js'

  export default {
    data (){
      return{
        type: 'month',
        mode: 'stack',
        weekday: [0, 1, 2, 3, 4, 5, 6],
        focus: '',
        listItem:"",
        schedule: SCHEDULE.items,
        events: [],
        selectedEvent: {},
        selectedElement: null,
        selectedOpen: false,
        firstInterval:9,
        eventTitle:{
          name:"任务名",
          source:"任务来源",
          state:"状 态",
          priority:"优先级",
          timeSpent:"花 费",
          start:"起始时间",
          end:"结束时间",
        },
      }
    },
    computed:{
      today(){
        let tDate = new Date();
        let tMonth = tDate.getMonth() + 1;
        return tDate.getFullYear()+"-"+tMonth+"-"+tDate.getDate()+" "+tDate.getHours()+":00:00"
      }
    },
    filters: {
      cookState(value) {
        if(value == 1){
          return "进行中"
        }else if(value == 2){
          return "未开始"
        }else if(value == 3){
          return "已完成"
        }
      },
      cookLength(value) {
        if(value && value.length >= 16){
          return value.substring(0, 17) + "...";
        }else{
          return value;
        }
        
      }
    },
    methods: {
      getEvents ({ start, end }) {
        let _this = this;
        _this.events = []
        let data = _this.schedule;
        const min = new Date(`${start.date}T00:00:00`)
        const max = new Date(`${end.date}T23:59:59`)
        const date = new Date();
        const month = date.getMonth() + 1;
        const year = date.getFullYear();
        for (let i = 0; i < data.length; i++) {
          let item = data[i];
          const startDate = item.start;
          const start = new Date(startDate);
          const startYear = start.getFullYear();
          const startMonth = start.getMonth() + 1;
          if(startYear == year && startMonth == month){
            _this.events.push({
              name: item.name,
              start: item.start,
              end: item.end,
              priority: item.priority,
              state: item.state,
              source:item.source,
              timeSpent:item.timeSpent,
              time:true,
            })
          }
        }
      },
      getEventColor (event) {
        let priority = event.priority;
        let state = event.state;
        if(state === 3){
          return "#E0E0E0"
        }else if(state === 1){
          if(priority == 1){
            return "#FF1744"
          }else if(priority == 2){
            return "#EF5350"
          }else if(priority == 3){
            return "#D500F9"
          }else if(3<priority<7){
            return "#3D5AFE"
          }else{
            return "#64B5F6"
          }
        }else if(state === 2){
          if(priority == 1){
            return "#FFA726"
          }else if(priority == 2){
            return "#FFF176"
          }else if(priority == 3){
            return "#B2FF59"
          }else if(3<priority<7){
            return "#84FFFF"
          }else{
            return "#E6EE9C"
          }
        }
        return "#B2DFDB"
      },
      viewDay ({ date }) {
        this.focus = date;
        this.type = 'day';
      },
      getToday () {
        let date = new Date();
        let month = date.getMonth() + 1;
        return date.getFullYear() +"-" + month + "-" + date.getDay();
      },
      showEvent ({ nativeEvent, event }) {
        const open = () => {
          this.selectedEvent = event
          this.selectedElement = nativeEvent.target
          setTimeout(() => this.selectedOpen = true, 10)
        }

        if (this.selectedOpen) {
          this.selectedOpen = false
          setTimeout(open, 10)
        } else {
          open()
        }

        nativeEvent.stopPropagation()
      },
      setToday(){
        this.focus = "";
        let date = new Date();
        this.firstInterval = date.getHours();
        this.type = 'day';
      },
      setType(value){
        this.type = value;
      }
    },
  }
</script>

<style lang="stylus" scoped>
.calendar-app
  width 100%
  height 100%
  position relative
  background-size cover
  background-image url("../assets/imgs/bg/spring.jpg")
.calendar-sheet
  margin-left 5%
  margin-top 30px
  opacity 0.8
</style>